@import './styles/variables';
@import './styles/global';




/* Header Styles */
#header {
  .head {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .logo-top {
    margin-left: 1em;
    margin-top: 3em;
  }

  .hamburger-icon {
    cursor: pointer;
    margin-top: 3em;
  }

  &.active {
    nav {
      display: flex;
    }

    .hamburger-icon {
      // content: url('../assets/images/icon-close.svg');
      content: url('./assets/images/icon-close.svg');
    }
  }
}

nav {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  position: absolute;
  border-radius: 5px;
  box-shadow: 0 0 10px #00000028;
  left: 1.2em;
  padding: 1.5em;
  width: calc(100% - 2.4em);
  max-width: 100%;
}

.nav-links {
  display: flex;
  flex-direction: column;
  gap: 1em;
  width: 100%;
  margin: auto;
}

.nav-link {
  text-align: center;
  width: 100%;
  margin: auto;
}

.nav-link ul {
  list-style: none;
  padding: 0;
  display: none;
  border-radius: 0.3em;
  background-color: #efeff1;
}

.nav-link li {
  width: 100%;
  line-height: 1em;
}

.nav-link button {
  width: 100%;
  font-size: 1em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.3em;
  padding: 0;
  border-width: 0;
  background-color: transparent;
  font-family: var(--ffamily-overpass);
  font-weight: var(--fweight-overpass-600);
  margin-bottom: 2em;
  cursor: pointer;
}

.nav-link button img {
  padding: 0 0.3em;
}

.open ul {
  display: block;
}

.open img {
  transform: rotate(180deg);
}

.divider {
  width: 100%;
  margin: 1em auto;
  height: 1px;
  background-color: #8f8f8f75;
}

.nav-btns {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 0.5em;
}

.nav-btns button {
  border-width: 0;
  background: none;
  font-family: var(--ffamily-ubuntu);
  font-weight: var(--fweight-ubuntu-700);
  font-size: 1em;
  padding: 1em;
  border-radius: 100px;
  cursor: pointer;
}

.nav-btns button:nth-of-type(1) {
  background-color: #fff;
  color: var(--Light-red-gradient-color);
}

.nav-btns button:nth-of-type(2) {
  border: 1px solid #fff;
  color: #fff;
  background-color: var(--Light-red-gradient-color);
}

.logo-top {
  margin-left: 1em;
  margin-top: 3em;
}





/* Sect-1 Section */
.sect-1 {
  background-image: url('./assets/images/bg-pattern-intro-mobile.svg'),
    linear-gradient(
      180deg,
      var(--Very-light-red-gradient-color),
      var(--Light-red-gradient-color)
    );
  background-size: cover; 
  background-position: center;
  height: 120vh;
  border-radius: 0 0 0 100px;
  position: relative;
  margin: 0 auto;
}

.head {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: calc(100% - 2.4em); 
  margin: 0 auto;
}

.cta {
  width: calc(100% - 2.4em); 
  margin: auto;
  margin-top: 6em;
}

.cta h1 {
  font-size: 1.9em;
}

.cta p {
  color: rgba(255, 255, 255, 0.7);
  text-align: center;
}

.sect-1 p {
  color: #fff;
  font-size: 1em;
  margin-top: 2em;
  font-family: var(--ffamily-overpass);
  font-weight: var(--fweight-overpass-300);
}

.btns {
  width: calc(100% - 2.4em);
  margin: 3em auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 0.5em;
}

button, a {
  cursor: pointer;
  text-decoration: none;
  padding: 0 1em;
  color: #000;
  font-weight: 400;
  text-align: justify;
}
button, li {
  line-height: 1;
  padding: 0.8em 0;
  text-align: center;
}

.btns button {
  border-width: 0;
  background: none;
  font-family: var(--ffamily-ubuntu);
  font-weight: var(--fweight-ubuntu-700);
  font-size: 1em;
  padding: 1em;
  border-radius: 100px;
}

.btns button:nth-of-type(1) {
  background-color: #fff;
  color: var(--Light-red-gradient-color);
}

.btns button:nth-of-type(2) {
  border: 1px solid #fff;
  color: #fff;
}

/* Sect-2 Section */
.sect-2 {
  width: calc(100% - 2.4em); 
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 2em;
  margin-top: 3em;
}
.sect-2 .info {
  margin-bottom: 3em;
}

.sect-2 p {
  font-size: 1em;
  font-family: var(--ffamily-overpass);
  font-weight: var(--fweight-overpass-300);
  line-height: 1.5em;
  color: var(--Very-dark-gray-blue-gradient-color);
}

/* Sect-3 Section */
.sect-3 {
  background-image: url('./assets/images/bg-pattern-circles.svg'),
    linear-gradient(
      180deg,
      var(--Very-dark-gray-blue-gradient-color),
      var(--Very-dark-desaturated-blue-gradient-color)
    );
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1em;
  height: 75%;
  border-radius: 0 100px;
  margin-top: 10em;
}

.sect-3 img {
  margin-top: -53%;
}

.sect-3 p {
  font-size: 1em;
  line-height: 1.5rem;
  color: #ffffffb7;
  font-family: var(--ffamily-overpass);
  font-weight: var(--fweight-overpass-300);
  padding-bottom: 6em;
}

/* Sect-4 Section */
.sect-4 {
  width: calc(100% - 2.4em); 
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.5em;
}

.sect-4 p {
  font-size: 1em;
  font-family: var(--ffamily-overpass);
  font-weight: var(--fweight-overpass-300);
  line-height: 1.5em;
  color: var(--Very-dark-gray-blue-gradient-color);
  margin-bottom: 4em;
}
.sect-4 img {
  grid-column: 1/3;
  content: url('./assets/images/illustration-laptop-mobile.svg');
  width: 100%;
  grid-row: 1/3;
  margin-left: 0em;
}

/* Footer Section */


footer {
  background-color: var(--Very-dark-black-blue-neutral-color);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2em;
  padding: 2.5em 0 0;
  border-radius: 0 100px;
  width: calc(100% - 2.4em); 
  margin: 0 auto;
}

.logo-footer {
    width: 90px;
    // margin-left: 13em;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;
}

.footer-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

footer h3 {
  margin-bottom: 1em;
}

footer li a {
  text-decoration: none;
  color: #ffffffb7;
}

footer li:hover {
  text-decoration: underline;
}


footer .attribution {
  font-size: 9px;
  text-align: center;
  grid-column: 1/5;
  margin-bottom: 2em;
  margin-left: 4em;
}

footer ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

footer li {
  line-height: 2em;
}

footer img {
  align-self: flex-start;
  justify-self: center;
}

/* Misc Styles */
.divider {
  width: 100%;
  margin: 1em auto;
  height: 1px;
  background-color: #8f8f8f75;
}

.nav-btns {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 0.5em;
}

.nav-btns button:nth-of-type(1) {
  width: 50%;
  padding: 0.8em;
  margin-top: 1em;
  background: none;
  border: 0;
}

.nav-btns button:nth-of-type(2) {
  background-image: linear-gradient(
    90deg,
    var(--Very-light-red-gradient-color),
    var(--Light-red-gradient-color)
  );

  width: 50%;
  padding: 0.8em;
  border-width: 0;
  border-radius: 100px;
  font-size: 1em;
  color: #fff;
  margin-bottom: 1em;
}

.btns button:nth-of-type(2):hover {
  background-color: var(--Very-light-red-primary-color);
  color: #fff;
}

button img {
  padding: 0 0.3em;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

header {
  nav {
    display: none;
  }

  &.active {
    nav {
      display: flex;
    }

    .hamburger-icon {
      content: url('./assets/images/icon-close.svg');
    }
  }
}

/* =============== 
  Media Queries   
=============== */
@media screen and (min-width: 992px) {
  header {
    display: grid;
    grid-template-columns: 10% 82%;
    width: 90%;
    margin: auto;

    nav {
      display: flex;
      position: relative;
      flex-direction: row;
      align-items: center;
      background: none;
      box-shadow: 0 0 0 transparent;
      left: 0;
      width: 100%;
      gap: 10em;
    }

    &.active nav {
      display: flex;
    }
  }

  .hamburger-icon {
    display: none;
  }

  .divider {
    display: none;
  }

  .logo-footer {
    width: 90px;
    margin-left: 13em;
}

  .nav-links {
    display: flex;
    flex-direction: row;
    width: 50%;
    gap: 2em;
  }

  .nav-link {
    width: 20%;
    margin: 0;
  }

  .nav-link ul {
    position: absolute;
    background-color: #fff;
    right: 1em;
    margin-top: 1em;
    width: max-content;
    padding-right: 3em;
    box-shadow: 0 0em rgb(255 255 255 / 33%);
  }

  .nav-links button {
    margin: 0;
    width: 30%;
    color: #fff;
    gap: 0;
  }

  .nav-btns {
    flex-direction: row;
    width: 30%;
    justify-content: end;
    margin-top: 3em;
  }

  nav button:hover {
    text-decoration: underline;
  }

  button, a { 
    text-align: justify;
}



  #header .logo-top {
    margin-left: 1em;
    margin-top: 0;
}

  .nav-btns button:nth-of-type(1) {
    width: 50%;
    color: #fff;
    padding: 0.8em;
    margin-top: -2em;
  }

  .nav-btns button:nth-of-type(2):hover {
    background-color: var(--Very-light-red-primary-color);
    color: #fff;
    text-decoration: none;
  }

  .nav-btns button:nth-of-type(2) {
    width: 50%;
    background-image: none;
    background-color: #fff;
    color: var(--Light-red-gradient-color);
    margin-bottom: 2em;
  }

  .btns button:nth-of-type(2):hover {
    background-color: var(--White-neutral-color);
    color: var(--Light-red-gradient-color);
  }

  .btns button:nth-of-type(1):hover {
    background-color: var(--Light-red-gradient-color);
    color: var(--White-neutral-color);
  }

  .nav-link img {
    content: url('./assets/images/icon-arrow-light.svg');
  }

  nav a:hover {
    color: rgba(0, 0, 0);
    font-weight: 600;
  }

  .nav-link {
    position: relative;
  }


  .sect-1 p {
    font-size: 1.1em;
  }

  .cta {
    margin-top: 10em;
  }

  .cta h1 {
    font-size: 3em;
  }

  .btns {
    width: 30%;
  }

  .sect-2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 20% 90%;
    width: 100%;
    margin-left: 2em;
  }

  .sect-2 img {
    content: url('./assets/images/illustration-editor-desktop.svg');
    width: 120%;
    grid-column: 2/3;
    grid-row: 1/3;
    align-self: flex-end;
    margin-left: 2em;
  }

  .sect-2 .title {
    grid-column: 1/3;
    grid-row: 1/2;
    text-align: center;
    font-size: 2em;
    align-self: end;
  }

  .sect-2 .info {
    margin: 0 auto;
    grid-column: 1/2;
    grid-row: 2/3;
    text-align: start;
  }

  .sect-2 .info:nth-of-type(1) {
    align-self: flex-start;
    margin-top: 3em;
  }

  .sect-2 .info:nth-of-type(2) {
    align-self: center;
  }

  .sect-3 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 120vh;
    background-size: auto;
  }

  .sect-3 h3 {
    text-align: center;
    font-size: 2.5em;
  }

  .sect-3 img {
    margin-top: -5em;
    align-self: flex-start;
    justify-self: center;
  }

  .sect-3 .info {
    margin-top: 17em;
  }

  .sect-4 {
    display: grid;
    grid-template-columns: 45% 55%;
    width: 100%;
    margin: 5rem 0;
  }

  .sect-4 img {
    grid-column: 1/2;
    content: url('./assets/images/illustration-laptop-desktop.svg');
    width: 100%;
    grid-row: 1/3;
  }

  .sect-4 .info:nth-of-type(2) {
    align-self: flex-start;
  }

  footer {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    text-align: start;
  }
    
  footer .footer-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    gap: 14em;
    margin-left: 15em;
  }

  footer .footer-section {
    align-items: flex-start;
  }

  footer .footer-section  ul li {
    text-align: justify;
}


  footer .attribution {
    font-size: 11px;
    text-align: center;
    grid-column: 1/5;
    margin-bottom: 2em;
  }
}

@media screen and (max-width: 991px) {
  footer ul,
  footer h4,
  footer li {
    align-items: center;
    text-align: center;
    align-self: center;
    justify-self: center;
  }

  footer .footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2em;
  }


  footer img {
    align-self: center;
    justify-self: center;
  }
}
